import { useTheme } from "hooks/use-theme";
import { Navigate, useParams } from "react-router-dom";
import { PostType } from "types/post";
import { getLinkPrefix } from "utils/get-link-prefix";
import { data } from "../news/data";
import { Article } from "./Article";

export const Layout = () => {
    useTheme("internal");
    const { category, slug } = useParams();
    const items = data;

    const activeArticle: PostType | undefined = items.find(
        (item) =>
            item.slug === slug &&
            getLinkPrefix(item.category) === "/" + category
    );

    if (activeArticle === undefined) {
        return <Navigate replace to="/not-found" />;
    }

    return <Article post={activeArticle} />;
};

export default Layout;
